.navigation {
  background-color: rgb(236, 188, 98);
  position: fixed;
  width: 20vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 150px;
  /* display: none; */
}

.avatar {
  border-radius: 50%;
  width: 50%;
}

.nav-item {
  font-family: "Yeon Sung", cursive;
  font-size: 2.5vw;
  color: antiquewhite;
  padding-top: 20px;
  text-decoration: none; /*a태크 밑줄 삭제*/
}

.nav-item:hover {
  color: black;
}

@media (max-width: 750) {
  .nav-item {
    font-size: 20px;
  }
}

.section {
  min-height: 100vh;
  padding-top: 30px;
}

.main {
  /* display: grid; */
  /* grid-template-columns: minmax(150px, 20vw) auto; */
  margin-left: 20vw;
}
.column-start {
  /* grid-column-start: 2; */
  padding: 10px;
  display: flex;
  flex-direction: column;
  height: 100vh;

}
.iframe-style{
  display: flex;
  flex: 1;
  width:100%;
  height:100vh;
}

